import React, { Component, Fragment } from 'react'

export default class Prices extends Component {
  state = {
    currency: 'USD'
  }
  render() {
    const key = this.props.bpi
    const country = key.map(country => (
      <li className="list-group-item">
        Bitcoin rate for {country.description}: <span className="badge badge-primary">{country.code}</span> <strong>{country.rate}</strong>
      </li>
    ))
    return (
      <Fragment>
        <ul className="list-group">
          {country}
        </ul>
      </Fragment>
    )
  }
}
